<template>
	<div id="takeout-detail">
		<public-header title="订单详情"></public-header>
		<div class="content" v-if="order.log_current">
			<router-link tag="div" class="order-log padding-15 bg-default font-15" :to="util.getUrl({path: 'pages/order/log', query: {id: order.id}})">
				<van-row class="flex-lr c-gray">
					<van-col span="2">
						<div class="icon icon-waimai font-20 c-primary"></div>
					</van-col>
					<van-col span="21">
						<div class="c-primary">{{order.log_current.title}}, 操作人: {{order.log_current.role_cn}}</div>
						<div class="font-13 margin-5-t">{{order.log_current.addtime_cn}}</div>
					</van-col>
					<van-col span="1">
						<div class="icon icon-right "></div>
					</van-col>
				</van-row>
			</router-link>
			<div class="tip">订单配送</div>
			<div class="address bg-default">
				<div class="type van-hairline--bottom c-gray" v-if="order.deliveryer_id > 0">{{order.delivery_collect_type_cn}}</div>
				<div class="address-detail">
					<div class="address-item buy flex-lr margin-15-b">
						<div class="left">
							<div class="circle"></div>
						</div>
						<div class="right" v-if="order.store">
							<div class="ellipsis">{{order.store.title}}</div>
							<div class="ellipsis margin-10-t c-gray font-14">{{order.store.address}}</div>
						</div>
					</div>
					<div class="address-item accept flex-lr">
						<div class="left">
							<div class="circle"></div>
						</div>
						<div class="right">
							<div class=" ellipsis">{{order.address}}</div>
							<div class=" ellipsis margin-10-t c-gray font-14">{{order.username}} {{order.mobile}}</div>
						</div>
					</div>
				</div>
			</div>
			<div class="telephone flex-lr bg-default margin-10-t padding-10-tb ">
				<div class="tel-item van-hairline--right" v-if="order.store" @click="util.jsTel(order.store.telephone)">
					<img src="static/img/store.png" alt="">
					<span>{{order.store.telephone}}</span>
				</div>
				<div class="tel-item" @click="util.jsTel(order.mobile)">
					<img src="static/img/customer.png" alt="">
					<span>{{order.mobile}}</span>
				</div>
			</div>
			<template v-if="order.deliveryer_id > 0 && order.deliveryer">
				<div class="tip">配送员信息</div>
				<van-row class="deliveryer-info bg-default padding-15-lr padding-10-tb flex-lr">
					<van-col span="4">
						<img :src="order.deliveryer.avatar" alt="">
					</van-col>
					<van-col span="12" class="padding-10-l">
						<div>{{order.deliveryer.title}}</div>
						<div class="c-gray font-14 margin-10-t ">{{order.deliveryer.mobile}}</div>
					</van-col>
					<van-col span="4" class="text-right">
						<div class="icon icon-telephone font-18" @click="util.jsTel(order.deliveryer.mobile)"></div>
					</van-col>
					<van-col span="4" class="text-right">
						<router-link tag="div" :to="util.getUrl({path: '/pages/order/location', query: {id: order.deliveryer.id}})" class="icon icon-location"></router-link>
					</van-col>
				</van-row>
			</template>
			<div class="tip">货品信息</div>
			<van-cell-group class="goods-info">
				<van-cell class="border-0px">
					<div slot="title">配送时间：{{order.delivery_day}} {{order.delivery_time}}</div>
					<div slot="right-icon">#{{order.serial_sn}}</div>
				</van-cell>
				<div class="note">
					<div class="left">备注:</div>
					<div class="right">{{order.note ? order.note : '无'}}</div>
				</div>
				<div class="goods-list padding-15-b font-14 van-hairline--bottom">
					<div class="goods-item margin-15-lr margin-15-t flex-lr" v-for="(item, index) in order.goods" :key="index">
						<div class="goods-title ellipsis">{{item.goods_title}}</div>
						<div class="goods-num">x{{item.goods_num}}</div>
						<div class="goods-price">¥{{item.goods_price}}</div>
					</div>
				</div>
				<div class="other padding-15-b font-14 c-gray">
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>配送费</div>
						<div>¥{{order.delivery_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>餐盒费</div>
						<div>¥{{order.box_price}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>包装费</div>
						<div>¥{{order.pack_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>合计</div>
						<div>¥{{order.total_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>优惠</div>
						<div>¥{{order.discount_fee}}</div>
					</div>
					<div class="other-item margin-15-lr margin-15-t flex-lr">
						<div>顾客实付</div>
						<div>¥{{order.final_fee}}</div>
					</div>
				</div>
			</van-cell-group>
			<div class="tip">本单收支</div>
			<van-cell-group class="goods-info">
				<van-cell title="本单平台共补贴" :value="'￥' + order.plateform_discount_fee" />
				<van-cell title="本单代理商共补贴" :value="'￥' + order.agent_discount_fee" />
				<van-cell title="商户实际入账" :value="'￥' + order.store_final_fee" />
				<van-cell title="代理商抽取佣金" :value="'￥' + order.plateform_serve_fee" />
				<template v-if="order.agentid > 0">
					<van-cell title="平台服务佣金(平台收取代理商)" :value="'￥' + order.agent_serve_fee" />
					<van-cell title="代理商实际收入" :value="'￥' + order.agent_final_fee" />
				</template>
			</van-cell-group>
			<div class="tip">订单信息</div>
			<van-cell-group class="order-info">
				<van-cell>
					<div slot="title">
						<span class="padding-15-r">订单编号</span>
						{{order.ordersn}}
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">
						<span class="padding-15-r">下单时间</span>
						{{order.addtime_cn}}
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">
						<span class="padding-15-r">支付方式</span>
						{{order.pay_type_cn}}
					</div>
				</van-cell>
			</van-cell-group>
			<div class="times van-hairline--bottom bg-default margin-10-t padding-15-lr padding-10-tb flex-lr font-14 text-center">
				<div class="time-item" v-if="order.delivery_assign_time_cn">
					<div class="time-title margin-10-b">接单</div>
					<div class="day">{{order.delivery_assign_time_cn.day}}</div>
					<div class="time">{{order.delivery_assign_time_cn.time}}</div>
				</div>
				<div class="time-item" v-if="order.delivery_instore_time_cn">
					<div class="time-title margin-10-b">到店</div>
					<div class="day">{{order.delivery_instore_time_cn.day}}</div>
					<div class="time">{{order.delivery_instore_time_cn.time}}</div>
				</div>
				<div class="time-item" v-if="order.delivery_takegoods_time_cn">
					<div class="time-title margin-10-b">取货</div>
					<div class="day">{{order.delivery_takegoods_time_cn.day}}</div>
					<div class="time">{{order.delivery_takegoods_time_cn.time}}</div>
				</div>
				<div class="time-item" v-if="order.delivery_success_time_cn">
					<div class="time-title margin-10-b">送达</div>
					<div class="day">{{order.delivery_success_time_cn.day}}</div>
					<div class="time">{{order.delivery_success_time_cn.time}}</div>
				</div>
			</div>
			<van-row class="btn-group bg-default">
				<template v-if="order.status < 5">
					<van-col span="8" v-if="order.is_pay == 1 && order.pay_type != 'delivery'">
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel_refund')">取消订单并退款</div>
					</van-col>
					<van-col span="8" v-else>
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'cancel')">取消订单</div>
					</van-col>
					<template v-if="order.status == 1">
						<van-col span="8">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'handle')">确定接单</div>
						</van-col>
						<van-col span="8">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'notify_clerk_handle')">通知商户</div>
						</van-col>
					</template>
					<template v-if="order.order_type == 1">
						<template v-if="order.status == 2 || order.status == 3">
							<van-col span="8">
								<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'notify_deliveryer_collect')">通知配送员抢单</div>
							</van-col>
							<van-col span="8" v-if="order.delivery_type == 2">
								<router-link tag="div" :to="util.getUrl({path: 'pages/order/dispatch', query: {id: order.id}})" class="btn-item bg-primary">调度</router-link>
							</van-col>
							<van-col span="8" v-if="order.uupaotui_status && order.delivery_type == 2">
								<div class="btn-item bg-primary" @click="onPushUUPaotui()">UU配送</div>
							</van-col>
						</template>
						<template v-if="order.status == 4">
							<van-col span="8">
								<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 're_notify_deliveryer_collect')">重新待抢订单</div>
							</van-col>
							<van-col span="8">
								<router-link tag="div" :to="util.getUrl({path: 'pages/order/dispatch', query: {id: order.id}})" class="btn-item bg-primary">重新调度</router-link>
							</van-col>
						</template>
						<template v-if="order.status > 1">
							<van-col span="8">
								<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'end')">完成订单</div>
							</van-col>
						</template>
					</template>
					<template v-else>
						<van-col span="8">
							<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'end')">完成订单</div>
						</van-col>
					</template>	
				</template>
				<template v-if="order.refund_status == 1">
					<van-col span="8">
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_handle')">发起退款</div>
					</van-col>
					<van-col span="8">
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status')">已退款</div>
					</van-col>
				</template>
				<template v-else-if="order.refund_status == 2">
					<van-col span="8">
						<div class="btn-item bg-primary" @click="onChangeOrderStatus(order, 'refund_query')">查询退款进度</div>
					</van-col>
					<van-col span="8">
						<div class="btn-item bg-danger" @click="onChangeOrderStatus(order, 'refund_status')">已退款</div>
					</van-col>
				</template>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import { changeOrderStatus, onPushUU } from "./controller/order.js"
	export default {
		data() {
			return {
				showPreLoading: true,
				order: {}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'plateform/order/takeout/detail',
					data: {
						id: this.$route.query.id
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return;
					}
					this.order = result.message.order;
				})
			},
			onChangeOrderStatus: function(order, type) {
				var params = {order: order, type: type, from: 'detail', vue: this};
				changeOrderStatus(params);
			},
			onPushUUPaotui() {
				var options = {
					vue: this,
					orderId: this.order.id
				};
				onPushUU(options);
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#takeout-detail .deliveryer-info img{
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}
	#takeout-detail .deliveryer-info .icon-location{
		width: 40px;
		height: 40px;
		line-height: 40px;
		border: 1px solid #e7e7e7;
		border-radius: 100%;
		background-color: #fff;
		color: #656565;
		text-align: center;
		font-size: 20px;
	}
	#takeout-detail .tip{
		padding: 10px 20px;
		font-size: 14px;
		color: #7a7a7a;
	}
	#takeout-detail .address .type{
		padding: 10px 15px;
	}
	#takeout-detail .address .address-detail{
		padding: 15px;
	}
	#takeout-detail .address .address-detail .address-item .left{
		width: 6%;
	}
	#takeout-detail .address .address-detail .address-item .right{
		width: 94%;
	}
	#takeout-detail .address .address-detail .address-item .circle{
		width: 10px;
		height: 10px;
		border-radius: 50%;
	}
	#takeout-detail .address .address-detail .address-item.buy .circle{
		background-color: #E87613;
	}
	#takeout-detail .address .address-detail .address-item.accept .circle{
		background-color: #4FAE52;
	}
	#takeout-detail .telephone .tel-item{
		width: 50%;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
		padding: 10px 0;
	}
	#takeout-detail .telephone .tel-item img{
		width: 24px;
		height: 24px;
		margin-right: 5px;
	}
	#takeout-detail .goods-info .note{
		background-color: #FECDAA;
		padding: 10px 15px;
		color: #7a7a7a;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		line-height: 1.4;
		font-size: 14px;
	}
	#takeout-detail .goods-info .note .left{
		width: 12%;
	}
	#takeout-detail .goods-info .note .right{
		width: 88%;
	}
	#takeout-detail .goods-info .goods-item .goods-title{
		width: 60%;
	}
	#takeout-detail .goods-info .goods-item .goods-num{
		width: 10%;
	}
	#takeout-detail .goods-info .goods-item .goods-price{
		width: 30%;
		text-align: right;
	}
	#takeout-detail .times .time-item .day,#takeout-detail .times .time-item .time{
		line-height: 1.2;
	}
	#takeout-detail .btn-group{
		width: 100%;
		padding: 7.5px;
	}
	#takeout-detail .btn-group .btn-item{
		margin: 7.5px;
		padding: 12px 0;
		border-radius: 5px;
		text-align: center;
		font-size: 14px;
	}
</style>


